import React, { useState, useEffect, useLayoutEffect } from 'react'

const useLayoutEffectDemo = () => {
  const [value, setValue] = useState(0)
  const [valueLayout, setValueLayout] = useState(0)

  useLayoutEffect(() => {
    if (valueLayout === 0) {
      setValueLayout(10 + Math.random() * 200)
    }
  }, [valueLayout])

  useEffect(() => {
    if (value === 0) {
      setValue(10 + Math.random() * 200)
    }
  }, [value])

  return (
    <div>
      <p>1. Âú® useEffect ‰∏≠ setValue ‰ºöÂá∫Áé∞Èó™ÁÉÅ</p>
      <div onClick={() => setValue(0)}>value: {value}</div>
      <p>2. Âú® useLayoutEffect ‰∏≠ setValue ‰∏ç‰ºöÂá∫Áé∞Èó™ÁÉÅ</p>
      <div onClick={() => setValueLayout(0)}>value: {valueLayout}</div>
    </div>
  )
}

export default useLayoutEffectDemo
